import React from 'react';
import Contact from '../Contact';

const styles = {
  footer: {
    position: 'fixed',
    left: 0,
    bottom: 0,
    width: '100%',
    display: 'flex',
    justifyContent: 'space-evenly'
  }
};
const Footer = () => {
  return (
    <div style={styles.footer}>
      <Contact />
    </div>
  )
};

export default Footer;



