import React, { Fragment } from 'react';
import CircularImage from '../../Components/CircularImage';
import Text  from '../../Components/Text';
import Summary from '../Summary';
import Footer from '../Footer';

const Home = () => {
  return (
    <Fragment>
      <CircularImage src="./profile_pic.jpg" />
      <Text text="Shobhit Chittora" type='heading' />
      <Text text="Full Stack Software Engineer | Front-end Dev | React | Redux | Angular | RxJS | Node" />
      <Summary />
      <Footer />
    </Fragment>
  )
};

export default Home;




